#maincontent .cot-form {
  margin-top: 10px; }
  #maincontent .cot-form form .panel {
    margin-bottom: 10px; }
    #maincontent .cot-form form .panel .panel-body {
      background-color: transparent; }
      #maincontent .cot-form form .panel .panel-body .form-group {
        margin-bottom: 10px; }
        #maincontent .cot-form form .panel .panel-body .form-group dl {
          margin-bottom: 0; }
          #maincontent .cot-form form .panel .panel-body .form-group dl dt {
            margin-bottom: 5px; }
          #maincontent .cot-form form .panel .panel-body .form-group dl dd {
            background-color: #eee;
            border-radius: 4px;
            color: #333;
            font-size: 14px;
            padding: 7px 13px; }
      #maincontent .cot-form form .panel .panel-body .grid-object {
        padding-bottom: 0;
        padding-top: 0; }
        #maincontent .cot-form form .panel .panel-body .grid-object.noTitle .grid-title {
          display: none; }
        #maincontent .cot-form form .panel .panel-body .grid-object table.grid-table {
          margin: 0 0 10px 0; }
          #maincontent .cot-form form .panel .panel-body .grid-object table.grid-table td {
            background-color: transparent; }
